import React from "react";
import css from "./ProfileTop.module.css";
import Button from "../Button/Button";
import AvatarEmpty from "../Svg/AvatarEmpty";
import ShareSvg from "../Svg/ShareSvg";
import PlusSvg from "../Svg/PlusSvg";
import MinusSvg from "../Svg/MinusSvg";
import { Form } from "react-router-dom";

function ProfileTop({
  profile,
  isCurrentUser,
  copyToClipboard,
  isAlreadyFollowed,
}) {
  const {
    // user_id,
    username,
    profile_image,
    profile_message,
    follower,
    following,
    response_rate,
  } = profile;
  return (
    <div className={css.ProfileTop}>
      <section className={css.section}>
        <div className={css.avatar}>
          {profile_image ? (
            <img className={css.img} src={profile_image} alt="avatar" />
          ) : (
            <AvatarEmpty />
          )}
        </div>
        <div className={css.info}>
          <p className={css.name}>{username}</p>
          <p className={css.introduce}>{profile_message}</p>
          <div className={css.btns}>
            <Button role="follower"> {follower.length}</Button>
            <Button role="following"> {following.length}</Button>
            {isCurrentUser ? (
              <span className={css.share}>
                <Button role="follow" onClick={copyToClipboard}>
                  <ShareSvg />
                </Button>
              </span>
            ) : (
              <Form method="post">
                {isAlreadyFollowed ? (
                  <Button role="follow" name="username" value={username}>
                    <MinusSvg />
                  </Button>
                ) : (
                  <Button role="follow" name="username" value={username}>
                    <PlusSvg />
                  </Button>
                )}
              </Form>
            )}
          </div>
          <div className={css.rate}>
            <div className={css.rateBar}>
              <p></p>
              <progress
                className={css.progress}
                id="answer-rate"
                value={response_rate}
                max="100"
              ></progress>
            </div>
            <div className={css.percent}>{response_rate}%</div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default React.memo(ProfileTop);
