import { redirect } from "react-router-dom";
import ChatService from "../api/ChatService";
import isLogin from "../utils/isLogin";

export default async function chatListLoader() {
  if (!isLogin()) {
    if (window.confirm("   .  ?")) {
      return redirect("/login");
    } else {
      throw 401;
    }
  } else {
    try {
      const { data } = await ChatService.fetchChatList();
      return data;
    } catch (err) {
      throw new Error(err);
    }
  }
}
