import { Client, AuthClient } from "./axiosInstance";

export default class UserService {
  static async fetchProfile(username) {
    return Client.get(`/users/profile/${username}`);
  }

  static async followUser(username) {
    return AuthClient().post(`/users/follow`, {
      username,
    });
  }

  static async updateMypage(formData) {
    return AuthClient({ "Content-Type": "multipart/form-data" }).put(`/users/profile`, formData);
  }
}