import { Client, AuthClient } from "../axiosInstance";

export default class AuthService {
  static signup(formData) {
    return Client.post("/users/register", formData);
    // Error 400 :   
  }

  static login(formData) {
    return Client.post("/users/login", formData);
  }

  static logout() {
    return AuthClient().get("/users/logout");
  }

  static verifyEmail(formData) {
    return Client.post(`/users/email/verify`, {
      username: formData.username,
      email: formData.email,
    });
  }
}