import UserService from "../api/UserService";
import useLocalStorage from "../utils/useLocalStorage";
import { redirect } from "react-router-dom";
import isLogin from "../utils/isLogin"; // Hack

export default async function mypageLoader() {
  if (!isLogin()) {
    if (window.confirm("  .  ?")) {
      return redirect("/");
    } else {
      //    
      window.history.back();
      return;
    }
  } else {
    try {
      const username = useLocalStorage.get(import.meta.env.VITE_NICKNAME);
      const { data } = await UserService.fetchProfile(username);
      return data;
    } catch (err) {
      console.error(err.response);
    }
  }
}
