import { Client, AuthClient } from "./axiosInstance";

export default class QuestionService {
  static async fetchAnsweredQuestions(username, page) {
    return Client.get(`/posts/question/${username}?page=${page}`);
  }

  static fetchUnAnsweredQuestions(page) {
    return AuthClient().get(`/posts/unanswered?page=${page}`);
  }

  static fetchRejectedQuestions(page) {
    return AuthClient().get(`/posts/rejected?page=${page}`);
  }

  static deleteQuestion(question_id) {
    return AuthClient().delete("/posts/question", {
      data: { question_id },
    });
  }

  static mutateQuestion(target_profile, content) {
    console.log({
      target_profile,
      content,
    });
    return Client.post("/posts/question", {
      target_profile,
      content,
    });
  }

  static mutateAnswer(question_id, content) {
    return AuthClient().post("/posts/answer", { question_id, content });
  }

  static mutateReject(question_id) {
    return AuthClient().post("/posts/rejected", { question_id });
  }
}