import css from "./QeustionCard.module.css";
import Button from "../../Button/Button";
import Input from "../../Input/Input";
import { elapsedText } from "../../../utils/dateFormat";
// SVG
import DotSvg from "../../Svg/DotSvg";
// import ImageSvg from "../../Svg/ImageSvg";
// import ClipSvg from "../../Svg/ClipSvg";
// import MailSvg from "../../Svg/MailSvg";
import ReportSvg from "../../Svg/ReportSvg";
import TrashSvg from "../../Svg/TrashSvg";
import FlagSvg from "../../Svg/FlagSvg";
import ArrowTurn from "../../Svg/ArrowTurn";
import { ArrowRight } from "../../Svg/ArrowSvg";

export default function QuestionCardView({
  nickname,
  content,
  answer_content,
  created_date,
  questionType,
  isCurrentUser,
  input,
  onInput,
  openRejectModal,
  onAnswer,
  showMenu,
  toggleCardMenu,
  openDeleteModal,
}) {
  return (
    <li className={css.QuestionCard}>
      <CardTop
        nickname={nickname}
        showMenu={showMenu}
        created_date={created_date}
        questionType={questionType}
        isCurrentUser={isCurrentUser}
        toggleCardMenu={toggleCardMenu}
        openRejectModal={openRejectModal}
        openDeleteModal={openDeleteModal}
      />
      <CardContent content={content} />
      {questionType === "answered" && (
        <AnswerCotent answer_content={answer_content} />
      )}
      {questionType === "unAnswered" && (
        <AnswerForm input={input} onInput={onInput} onAnswer={onAnswer} />
      )}
    </li>
  );
}

export function CardTop({
  nickname,
  created_date,
  showMenu,
  toggleCardMenu,
  questionType,
  isCurrentUser,
  openRejectModal,
  openDeleteModal,
}) {
  return (
    <div className={css.CardTop}>
      <div className={css.topLeft}>
        {/* <div className={css.avatar}></div> */}
        <div className={css.nameField}>
          <p className={css.nickname}>{nickname}</p>
          <p className={css.created_date}>{elapsedText(created_date)}</p>
        </div>
      </div>

      <div className={css.topRight}>
        <Button onClick={toggleCardMenu}>
          <DotSvg />
          {showMenu && (
            <CardMenu
              questionType={questionType}
              isCurrentUser={isCurrentUser}
              openDeleteModal={openDeleteModal}
              openRejectModal={openRejectModal}
            />
          )}
        </Button>
      </div>
    </div>
  );
}

function CardContent({ content }) {
  return <p className={css.CardContent}>{content}</p>;
}

function AnswerCotent({ answer_content }) {
  return (
    <>
      <br />
      <p className={css.AnswerContent}>
        <ArrowTurn /> {answer_content}
      </p>
    </>
  );
}

function AnswerForm({ input, onInput, onAnswer }) {
  return (
    <form onSubmit={onAnswer}>
      <div className={css.answerField}>
        <Input
          name="content"
          value={input.content}
          onChange={onInput}
          role="answerForm"
          placeholder=" ."
        />
        <Button>
          <ArrowRight />
        </Button>
      </div>
    </form>
  );
}

function CardMenu({
  isCurrentUser,
  questionType,
  openDeleteModal,
  openRejectModal,
}) {
  return (
    <ul className={css.CardMenu}>
      {/* {questionType !== "rejected" && (
        <li>
           <ClipSvg />
        </li>
      )} */}
      {/* <li onClick={() => alert(" .")}>
          <ImageSvg />
      </li> */}
      {/* {questionType === "answered" && (
        <li onClick={() => alert(" .")}>
            <MailSvg />
        </li>
      )} */}
      {isCurrentUser && questionType === "unAnswered" && (
        <li onClick={openRejectModal}>
            <ReportSvg />{" "}
        </li>
      )}
      {isCurrentUser && (
        <li onClick={openDeleteModal}>
            <TrashSvg />{" "}
        </li>
      )}
      <li onClick={() => alert(" .")}>
         <FlagSvg />
      </li>
    </ul>
  );
}
