export default function QuestionReducer(state, action) {
  switch (action.type) {
    case "INIT_QUESTION_COUNT":
      return {
        ...state,
        questionCount: {
          answered: action.payload.answered,
          unAnswered: action.payload.unanswered,
          rejected: action.payload.rejected,
        },
      };
    //   
    case "GET_ANSWERED":
      return {
        ...state,
        answered: {
          next: action.payload.next,
          results: state.answered.results
            ? state.answered.results.concat(action.payload.results)
            : action.payload.results,
        },
      };
    // 
    case "POST_ANSWER":
      return {
        ...state,
        answered: {
          next: 1,
          results: [],
        },
        unAnswered: {
          next: 1,
          results: [],
        },
        questionCount: {
          ...state.questionCount,
          unAnswered: state.questionCount.unAnswered - 1,
          answered: state.questionCount.answered + 1,
        },
      };
    //   
    case "GET_UNANSWERED":
      return {
        ...state,
        unAnswered: {
          next: action.payload.next,
          results:
            state.unAnswered.results !== null
              ? state.unAnswered.results.concat(action.payload.results)
              : action.payload.results,
        },
      };
    //   
    case "POST_QUESTION":
      return {
        // hack
        ...state,
        unAnswered: {
          next: 1,
          results: [],
        },
        questionCount: {
          ...state.questionCount,
          unAnswered: state.questionCount.unAnswered + 1,
        },
      };
    //   
    case "GET_REJECTED":
      return {
        ...state,
        rejected: {
          next: action.payload.next,
          results: state.rejected.results
            ? state.rejected.results.concat(action.payload.results)
            : action.payload.results,
        },
      };
    // 
    case "POST_REJECT":
      return {
        ...state,
        unAnswered: {
          next: 1,
          results: [],
        },
        rejected: {
          next: 1,
          results: [],
        },
        questionCount: {
          ...state.questionCount,
          unAnswered: state.questionCount.unAnswered - 1,
          rejected: state.questionCount.rejected + 1,
        },
      };
    // -----  -----
    case "DELETE_QUESTION":
      const cpy = { ...state };
      const currentType = action.payload.questionType;
      const target_pk = action.payload.question_id;
      const new_results = cpy[action.payload.questionType].results.filter(
        (data) => data.pk !== target_pk
      );
      const new_count = cpy.questionCount[currentType] - 1;

      cpy[currentType] = {
        ...cpy[currentType],
        results: new_results,
      };
      cpy["questionCount"] = {
        ...cpy["questionCount"],
        [currentType]: new_count,
      };

      return cpy;

    default:
      throw new Error(`Unhandled action type: ${action.type}`);
  }
}
