import InputView from "./InputView";

export default function Input({
  name,
  value,
  onChange,
  REF,
  role,
  type,
  maxLength,
  disabled,
  required,
  placeholder,
}) {
  const props = {
    name,
    value,
    onChange,
    REF,
    role,
    type,
    maxLength,
    disabled,
    required,
    placeholder,
  };

  return <InputView {...props} />;
}
