import QuestionService from "../QuestionService";
import { useMutation } from "@tanstack/react-query";
import { useToast2 } from "../../context/ToastContext";
import { useQuestionContext } from "../../context/QuestionContext";

export default function useMutateQuestion(username, content) {
  const openToast = useToast2();
  const [, dispatch] = useQuestionContext();

  return useMutation(() => QuestionService.mutateQuestion(username, content), {
    onSuccess: () => {
      openToast(" .");
      dispatch({ type: "POST_QUESTION" });
    },
    onError: (error) => {
      openToast(error.response.data.error);
    },
  });
}
