import css from "./Input.module.css";

export default function InputView({
  name,
  value,
  onChange,
  REF,
  role,
  type,
  maxLength,
  placeholder,
  required,
  disabled,
}) {
  return (
    <input
      name={name}
      value={value}
      onChange={onChange}
      ref={REF}
      type={type || "text"}
      className={`${css.Input} ${css[role]}`}
      maxLength={maxLength}
      placeholder={placeholder}
      required={required}
      disabled={disabled}
    />
  );
}
