import { useState, useCallback } from "react";
import { useLoaderData } from "react-router-dom";
import { elapsedText } from "../../utils/dateFormat";
import DotSvg from "../../components/Svg/DotSvg";
import css from "./ChatList.module.css";

export default function ChatList() {
  const { results: chatList } = useLoaderData();

  if (!chatList) {
    return null;
  }
  if (chatList?.error) {
    return <h4>no ChatList</h4>;
  }
  if (chatList.length === 0) {
    return <h4>no ChatList</h4>;
  }

  return (
    <div className={css.wrap}>
      {chatList.map((chat) => (
        <ListItem key={chat.pk} chat={chat} />
      ))}
    </div>
  );
}

function ListItem({ chat }) {
  const [showMenu, setShowMenu] = useState(false);

  const toggleChatMenu = useCallback(() => {
    setShowMenu((prev) => !prev);
  }, []);

  return (
    <div className={css.listItem}>
      <div className={css.avatar}></div>
      <div className={css.content}>
        <p className={css.nickname}>{chat.nickname}</p>
        <p className={css.msg}>{chat.last_message}</p>
      </div>
      <div className={css.utils}>
        <p className={css.time}>{elapsedText(chat.created_date)}</p>
        <span className={css.dotBtn} onClick={toggleChatMenu}>
          <DotSvg />
          {showMenu && <ChatMenu />}
        </span>
      </div>
    </div>
  );
}

function ChatMenu({ handleDelete }) {
  return (
    <div className={css.menu}>
      <div></div>
      <div></div>
      <div className={css.del}> </div>
    </div>
  );
}
