import { useState, useCallback } from "react";
import { useQuestionContext } from "../../../context/QuestionContext";
import useReject from "../../../hooks/useReject";
import useInput from "../../../hooks/useInput";
import useAnswer from "../../../hooks/useAnswer";
import useDeleteQuestion from "../../../hooks/useDeleteQuestion";
import QuestionCardView from "./QuestionCardView";
import { useModal } from "../../../context/ModalContext";
import CardModal from "../../Modal/CardModal/CardModal";
import useToast from "../../../hooks/useToast";

export default function QuestionCard({
  question,
  questionType,
  isCurrentUser,
}) {
  const {
    pk,
    nickname,
    content,
    created_date,
    answer_content,
    // chat_room
  } = question;
  const [type, { open, close }] = useModal();
  const [, dispatch] = useQuestionContext();
  const [input, onInput, onReset] = useInput({ content: "" });
  const [showMenu, setShowMenu] = useState(false);
  const openToast = useToast();

  const toggleCardMenu = useCallback(() => {
    setShowMenu((prev) => !prev);
  }, []);

  const onAnswer = async (e) => {
    e.preventDefault();
    const res = await useAnswer(dispatch, pk, input.content);
    if (res.isSuccess) {
      openToast(res.desc);
    }
    onReset();
  };

  const onReject = useCallback(() => {
    useReject(dispatch, pk);
    openToast(" .");
  }, [dispatch, pk, type]);

  const onDelete = async () => {
    await useDeleteQuestion(dispatch, pk, questionType);
    openToast(" .");
  };

  const openDeleteModal = () => {
    open(
      <CardModal
        role="deleteQuestion"
        title="  ?"
        desc="     ."
        buttonText={{ cancel: "", confirm: "" }}
        onClose={close}
        onSuccess={onDelete}
      />
    );
  };

  const openRejectModal = () => {
    open(
      <CardModal
        role="rejectQuestion"
        title="  ?"
        desc="       ."
        buttonText={{ cancel: "", confirm: "" }}
        onClose={close}
        onSuccess={onReject}
      />
    );
  };

  const props = {
    nickname,
    content,
    answer_content,
    created_date,
    questionType,
    isCurrentUser,
    input,
    onInput,
    onAnswer,
    showMenu,
    toggleCardMenu,
    openDeleteModal,
    openRejectModal,
  };

  return <QuestionCardView {...props} />;
}
