import React, { useCallback } from "react";
import css from "./Login.module.css";
import Button from "../../components/Button/Button";
import Input from "../../components/Input/Input";
import Warning from "../../components/Warning/Warning";
import Logo from "../../components/Logo/Logo";
import { useNavigate } from "react-router-dom";

export default function LoginView({
  input,
  onInput,
  onSubmit,
  isLoading,
  isValidPassword,
  filteredUserName,
}) {
  return (
    <main>
      <section className={css.loginPage}>
        <Logo />
        <form onSubmit={onSubmit} className={css.inputs}>
          <div>
            <Input
              name="username"
              value={filteredUserName}
              required={true}
              onChange={onInput}
              placeholder=""
            />
          </div>
          <div>
            <Input
              type="password"
              name="password"
              value={input.password}
              onChange={onInput}
              required={true}
              placeholder=""
            />
            {!isValidPassword && <Warning>///8~20</Warning>}
          </div>
          <MemoizedButtons isLoading={isLoading} />
        </form>
      </section>

      <div className={css.cheese}>
        <p> Cheese Co.</p>
      </div>
    </main>
  );
}

const MemoizedButtons = React.memo(({ isLoading }) => {
  const navigate = useNavigate();
  const goBack = useCallback(() => navigate(-1), [navigate]);

  return (
    <div className={css.btns}>
      <Button type="button" role="loginBack" onClick={goBack}>
        
      </Button>
      <Button type="submit" role="login" disabled={isLoading}>
        {isLoading ? <div className={css.spinner} /> : ""}
      </Button>
    </div>
  );
});
