import useLocalStorage from "./useLocalStorage";

export default function handleUnAuthorized(err) {
  if (err.response.status === 401) {
    alert(" .   .");
    useLocalStorage.remove(import.meta.env.VITE_TOKEN_KEY_NAME);
    useLocalStorage.remove(import.meta.env.VITE_NICKNAME);
    window.location.href = "/login";
  }
}
