import { useState } from "react";

export default function useInput(initialInput) {
  const [input, setInput] = useState(initialInput);

  const onInput = (e) => {
    const { name, value } = e.target;

    setInput({
      ...input,
      [name]: value,
    });
  };

  const onReset = () => {
    setInput(initialInput);
  };

  return [input, onInput, onReset];
}
