const useLocalStorage = {
  get: (KEY) => {
    try {
      return window.localStorage.getItem(KEY);
    } catch (err) {
      console.error(err);
    }
  },
  set: (KEY, VALUE) => {
    try {
      window.localStorage.setItem(KEY, VALUE);
    } catch (err) {
      console.error(err);
    }
  },
  remove: (KEY) => {
    try {
      window.localStorage.removeItem(KEY);
    }
    catch (err) {
      console.error(err);
    }
  }
};

export default useLocalStorage;