import React, { useState, useMemo, useContext } from "react";

const ModalContext = React.createContext();
const ModalAction = React.createContext();

export function ModalProvider({ children }) {
  const [component, setComponent] = useState(null);

  const actions = useMemo(
    () => ({
      open(component) {
        setComponent(component);
      },
      close() {
        setComponent(null);
      },
    }),
    []
  );

  return (
    <ModalAction.Provider value={actions}>
      <ModalContext.Provider value={component}>
        {children}
      </ModalContext.Provider>
    </ModalAction.Provider>
  );
}

export function useModal() {
  const component = useContext(ModalContext);
  const actions = useContext(ModalAction);

  if (component === undefined) {
    throw new Error("modal state is undefined");
  }

  if (actions === undefined) {
    throw new Error("modal actions is undefined");
  }

  return [component, actions];
}
