import React from "react";
import css from "./QuestionCount.module.css";

function QuestionCountView({ questionCount, questionType, onCountClick }) {
  const { answered, rejected, unAnswered } = questionCount;
  return (
    <section className={css.QuestionCount}>
      <div
        className={questionType === "answered" ? css["sharpen"] : css["blur"]}
      >
        <div className={css.card} onClick={onCountClick.showAnswered}>
          <span className={css.count}>{answered}</span>
          <p className={css.done}> </p>
        </div>
      </div>

      <div
        className={questionType === "unAnswered" ? css["sharpen"] : css["blur"]}
      >
        <div className={css.card} onClick={onCountClick.showUnAnswered}>
          <span className={css.count}>{unAnswered}</span>
          <p className={css.yet}></p>
        </div>
      </div>

      <div
        className={questionType === "rejected" ? css["sharpen"] : css["blur"]}
      >
        <div className={css.card} onClick={onCountClick.showRejected}>
          <span className={css.count}>{rejected}</span>
          <p className={css.reject}> </p>
        </div>
      </div>
    </section>
  );
}

export default React.memo(QuestionCountView);
