import React, { useEffect, useMemo, useState } from "react";
import SignUpView from "./SignUpView";
import useInput from "../../hooks/useInput";
import UserService from "../../api/UserService";
import validation, { REGEX } from "../../utils/validation";
import useSignup from "../../api/queries/useSignup";
import useVerifyEmail from "../../api/queries/useVerifyEmail";

export default function SignUp() {
  const [emailCodeDisable, setEmailCodeDisable] = useState(false);
  const [duplicateBtnDisable, setDuplicateBtnDisable] = useState(false);
  const [toastState, setToastState] = useState({
    show: false,
    desc: "",
  });
  const [input, onInput] = useInput({
    username: "",
    email: "",
    password: "",
    password2: "",
    verification_code: "",
  });

  const { mutate: requestEmailCode } = useVerifyEmail(input);
  const { mutate: signupRequest, isLoading } = useSignup(input);

  useEffect(() => {
    if (duplicateBtnDisable) {
      setDuplicateBtnDisable(false);
    }
  }, [input.username, duplicateBtnDisable]);

  useEffect(() => {
    if (emailCodeDisable) {
      setEmailCodeDisable(false);
    }
  }, [input.email, emailCodeDisable]);

  const sendDuplicateNameCheck = async () => {
    try {
      const res = await UserService.fetchProfile(input.username);
      if (res.status === 200) {
        // Disable
        openToast("   .");
      }
    } catch (err) {
      if (err.response.status === 400) {
        // Able
        openToast("   .");
        setDuplicateBtnDisable(true);
      }
    }
  };

  const openToast = (desc) => {
    setToastState({
      show: true,
      desc,
    });
    setTimeout(() => {
      setToastState({
        show: false,
        desc: "",
      });
    }, 2000);
  };

  const sendEmailCode = async () => {
    requestEmailCode(input);
    setEmailCodeDisable(true);
  };

  const onSubmit = (e) => {
    e.preventDefault();
    signupRequest(input);
  };

  const isInvalidEmail = useMemo(
    () => !validation.isValidEmail(input.email),
    [input.email]
  );
  const isInValidPassword = useMemo(
    () => !validation.isValidPassword(input.password),
    [input.password]
  );
  const isInValidConfirmPassword = useMemo(
    () => !(input.password === input.password2),
    [input.password, input.password2]
  );
  const filteredUserName = useMemo(
    () => input.username.replace(REGEX.username, ``),
    [input.username]
  );

  const props = {
    input,
    onInput,
    isLoading,
    onSubmit,
    toastState,
    sendEmailCode,
    sendDuplicateNameCheck,
    duplicateBtnDisable,
    emailCodeDisable,
    filteredUserName,
    isInvalidEmail,
    isInValidPassword,
    isInValidConfirmPassword,
  };

  return <SignUpView {...props} />;
}
