import css from "./Button.module.css";

export default function ButtonView({
  type,
  role,
  value,
  name,
  onClick,
  disabled,
  children,
}) {
  return (
    <button
      type={type}
      onClick={onClick ? onClick : null}
      value={value}
      name={name}
      className={`${css[role]} `}
      disabled={disabled}
    >
      {children}
    </button>
  );
}
