import QuestionService from "../api/QuestionService";

export default async function useAnswered(dispatch, username, page) {
  try {
    const { data } = await QuestionService.fetchAnsweredQuestions(
      username,
      page
    );
    dispatch({ type: "GET_ANSWERED", payload: data });
  } catch (err) {
    console.error(`${err} :   `);
  }
}
