import { useMutation } from "@tanstack/react-query";
import { useToast2 } from "../../context/ToastContext";
import UserService from "../UserService";
import handleUnAuthorized from "../../utils/handleUnAuthorized";
import { useAuth } from "../../context/AuthContext";

export default function useUpdateMypage() {
  const openToast = useToast2();
  const [, { setUsername }] = useAuth();

  return useMutation((formData) => UserService.updateMypage(formData), {
    onSuccess: (data, variables) => {
      let newUsername = getNameFromFormData(variables);
      if (newUsername) {
        setUsername(newUsername);
      }
      openToast("  .");

      // local storage  .
    },
    onError: (error) => {
      if (error.response.status === 401) {
        handleUnAuthorized(error);
        return;
      }
      openToast(error.response.data.error);
    },
  });
}

function getNameFromFormData(formData) {
  let username;

  for (let data of formData.entries()) {
    if (data[1]) {
      if (data[0] === "username") {
        username = data[1];
      }
    }
  }

  return username;
}
