import QuestionService from "../api/QuestionService";

export default async function useDeleteQuestion(
  dispatch,
  question_id,
  questionType
) {
  try {
    const { data } = await QuestionService.deleteQuestion(question_id);
    dispatch({
      type: "DELETE_QUESTION",
      payload: { question_id, questionType },
    });
  } catch (err) {
    if (err.response.status === 401) {
      alert(" .    .");
    }
  }
}
