import { useEffect } from "react";
import css from "./CardModal.module.css";
import Button from "../../Button/Button";
import { useModal } from "../../../context/ModalContext";
import { enableScroll, disableScroll } from "../../../utils/scroll";

export default function CardModal({
  role,
  title,
  desc,
  buttonText,
  onClose,
  onSuccess,
}) {
  const [, { close }] = useModal();

  useEffect(() => {
    disableScroll();
    return enableScroll;
  }, []);

  return (
    <>
      <div onClick={close} className="modalContainer"></div>
      <div className={css.CardModal}>
        <h3 className={css.title}>{title}</h3>
        <p className={css.desc}>{desc}</p>
        <div className={css.btns}>
          {buttonText.cancel && (
            <Button role="cancel" onClick={onClose || close}>
              {buttonText.cancel}
            </Button>
          )}
          {buttonText.confirm && (
            <Button role={role} onClick={onSuccess}>
              {buttonText.confirm}
            </Button>
          )}
        </div>
      </div>
    </>
  );
}
