import { useMutation } from "@tanstack/react-query";
import AuthService from "../auth/AuthService";
import { useToast2 } from "../../context/ToastContext";

export default function useVerifyEmail(input) {
  const openToast = useToast2();

  return useMutation(() => AuthService.verifyEmail(input), {
    onSuccess: ({ data }) => {
      openToast(data.info);
    },
    onError: (error) => {
      openToast(error.response.data.error);
    },
  });
}
