import axios from "axios";
import useLocalStorage from "../utils/useLocalStorage";

let baseURL = import.meta.env.VITE_API_ENDPOINT_PROD;
if (import.meta.env.MODE === "development") {
  baseURL = import.meta.env.VITE_API_ENDPOINT_DEV;
}
if (window.location.hostname.includes("vercel")) {
  baseURL = import.meta.env.VITE_API_ENDPOINT_DEV;
}
let timeout = 6000;

export const axiosClient = (options) => {
  const instance = axios.create({
    baseURL,
    timeout,
    ...options
  });
  return instance;
}

export const AuthClient = (headerOptions, options) => {
  const token = useLocalStorage.get(import.meta.env.VITE_TOKEN_KEY_NAME);
  const instance = axios.create({
    baseURL,
    timeout,
    headers: {
      Authorization: "Token " + token,
      ...headerOptions,
    },
    ...options,
  });
  return instance;
}

export const Client = axiosClient();