import React, { useEffect, useCallback, useMemo } from "react";
import SnbView from "./SnbView";
import { useModal } from "../../context/ModalContext";
import { useNavigate } from "react-router-dom";
import isLogin from "../../utils/isLogin";
import { enableScroll, disableScroll } from "../../utils/scroll";
import CardModal from "../Modal/CardModal/CardModal";

function Snb() {
  const navigate = useNavigate();
  const [type, { open, close }] = useModal();

  useEffect(() => {
    disableScroll();
    return enableScroll;
  }, []);

  const closeSnb = useCallback(() => {
    close();
  }, []);

  const restrictedUrls = useMemo(() => {
    return {
      "/mypage": true,
      "/chatlist": true,
    };
  }, []);

  const goTo = useCallback(
    (url) => {
      if (restrictedUrls[url]) {
        console.log(!isLogin());
        if (!isLogin()) {
          open(
            <CardModal
              role="confirm"
              title=" ?"
              desc="    ."
              buttonText={{ cancel: "", confirm: "" }}
              onClose={close}
              onSuccess={() => {
                navigate("/login");
                close();
              }}
            />
          );
          return;
        }
      }
      closeSnb();
      navigate(url);
    },
    [isLogin]
  );

  const props = {
    type,
    goTo,
    closeSnb,
  };

  return <SnbView {...props} />;
}

export default React.memo(Snb);

