import React, { useCallback } from "react";
import { useParams } from "react-router-dom";
import css from "./QuestionForm.module.css";
import Input from "../Input/Input/";
import Button from "../Button/Button";
import useInput from "../../hooks/useInput";
import useMutateQuestion from "../../api/queries/useMutateQuestion";

function QuestionForm() {
  const { username } = useParams();
  const [input, onInput, onReset] = useInput({ content: "" });
  const { mutate: sendQuestion } = useMutateQuestion(username, input.content);

  const onSubmit = useCallback(
    async (e) => {
      e.preventDefault();
      sendQuestion();
      onReset();
    },
    [sendQuestion, onReset]
  );

  return (
    <form onSubmit={onSubmit}>
      <div className={css.QuestionForm}>
        <div className={css.formField}>
          <Input
            name="content"
            value={input.content}
            onChange={onInput}
            role="question"
            required={true}
            maxLength="100"
            placeholder="  !"
          />
          <Button type="submit" role="questionSubmit">
            
          </Button>
        </div>
      </div>
    </form>
  );
}

export default React.memo(QuestionForm);
