import { useMutation } from "@tanstack/react-query";
import { useNavigate } from "react-router-dom";
import AuthService from "../auth/AuthService";
import { useAuth } from "../../context/AuthContext";
import { useToast2 } from "../../context/ToastContext";

export default function useLogin(input) {
  const navigate = useNavigate();
  const [, { login }] = useAuth();
  const openToast = useToast2();

  return useMutation(() => AuthService.login(input), {
    onSuccess: ({ data }) => {
      login(data);
      navigate(`/${data.username}`);
    },
    onError: (error) => {
      openToast(
        error?.response?.data?.error[0] || error?.response?.data?.error
      );
    },
  });
}
