import React, { useMemo, useCallback } from "react";
import LoginView from "./LoginView";
import useInput from "../../hooks/useInput";
import validation from "../../utils/validation";
import useLogin from "../../api/queries/useLogin";
import { REGEX } from "../../utils/validation";

export default function Login() {
  const [input, onInput] = useInput({ username: "", password: "" });
  const { mutate: requestLogin, isLoading } = useLogin(input);

  const onSubmit = useCallback(
    (e) => {
      e.preventDefault();
      requestLogin(input);
    },
    [input, requestLogin]
  );

  const isValidPassword = useMemo(
    () =>
      input.password.length === 0 || validation.isValidPassword(input.password),
    [input.password]
  );

  const filteredUserName = useMemo(
    () => input.username.replace(REGEX.username, ``),
    [input.username]
  );

  const props = {
    input,
    onInput,
    onSubmit,
    isLoading,
    isValidPassword,
    filteredUserName,
  };

  return <LoginView {...props} />;
}
