import QuestionService from "../api/QuestionService";
import handleUnAuthorized from "../utils/handleUnAuthorized";

export default async function useUnAnswered(dispatch, page) {
  try {
    const { data } = await QuestionService.fetchUnAnsweredQuestions(page);
    dispatch({ type: "GET_UNANSWERED", payload: data });
  } catch (err) {
    if (err.response.status === 401) {
      handleUnAuthorized(err);
    }
  }
}
