import { createContext, useContext, useReducer } from "react";
import QuestionReducer from "./QuestionReducer";

const QuestionContext = createContext(null);
const QuestionDispatch = createContext(null);

const initialState = {
  questionCount: {
    answered: 1,
    unAnswered: 1,
    rejected: 1,
  },
  answered: {
    next: 1,
    results: null,
  },
  unAnswered: {
    next: 1,
    results: null,
  },
  rejected: {
    next: 1,
    results: null,
  },
};

export const QuestionProvider = ({ children }) => {
  const [state, dispatch] = useReducer(QuestionReducer, initialState);

  return (
    <QuestionContext.Provider value={state}>
      <QuestionDispatch.Provider value={dispatch}>
        {children}
      </QuestionDispatch.Provider>
    </QuestionContext.Provider>
  );
};

export const useQuestionContext = () => {
  const state = useContext(QuestionContext);
  const dispatch = useContext(QuestionDispatch);

  if (!state) {
    throw new Error(`Question Context Error :   .`);
  }

  if (!dispatch) {
    throw new Error(`Question Reducer Error :   .`);
  }

  return [state, dispatch];
};
