import React from "react";
import css from "./Mypage.module.css";
import Button from "../../components/Button/Button";
import Pencil from "../../components/Svg/Pencil";
import CheckSvg from "../../components/Svg/CheckSvg";
import Input from "../../components/Input/Input";
import CameraSvg from "../../components/Svg/CameraSvg";

export default function MypageView({
  loaderData,
  input,
  onInput,
  editable,
  thumbnail,
  changeImg,
  editMypage,
  openLogoutModal,
}) {
  return (
    <form onSubmit={editMypage}>
      <div className={css.wrap}>
        <div className={css.pro_img}>
          {!editable ? (
            <img
              src={thumbnail || loaderData.profile_image}
              className={css.img}
              alt="avatar"
            />
          ) : (
            <>
              <label htmlFor="profile_image_input" className={css.imgLabel}>
                {thumbnail ? (
                  <img src={thumbnail} className={css.img} alt="avatar" />
                ) : (
                  <CameraSvg />
                )}
              </label>
              {/* file */}
              <input
                id="profile_image_input"
                className={css.imgInput}
                type="file"
                accept="image/*"
                name="profile_image"
                onChange={changeImg}
              />
            </>
          )}
        </div>
        <div className={css.nameWrap}>
          <Input
            name="username"
            value={input.username}
            onChange={onInput}
            role="mypageName"
            disabled={!editable}
            className={editable ? css["nameField"] : ""}
            placeholder={loaderData.username}
          />
          <Button
            onClick={editMypage}
            role={editable ? "editable" : "unEditable"}
          >
            {editable ? <CheckSvg /> : <Pencil />}
          </Button>
        </div>

        <div className={css.introduce}>
          <Input
            name="profile_message"
            role="mypageProfileMsg"
            value={input.profile_message || ""}
            onChange={onInput}
            disabled={!editable}
            className={editable ? css["msgFieldOn"] : css["msgField"]}
            placeholder={loaderData.profile_message}
          />
        </div>

        <Btns follower={loaderData.follower} following={loaderData.following} />

        <Bottom openLogoutModal={openLogoutModal} />
      </div>
    </form>
  );
}

const Btns = React.memo(({ follower, following }) => {
  return (
    <div className={css.btns}>
      <div className={css.follower}> {follower.length}</div>
      <div className={css.following}> {following.length}</div>
    </div>
  );
});

const Bottom = React.memo(({ openLogoutModal }) => {
  return (
    <div className={css.bottom}>
      <div className={css.btn}> </div>
      <div className={css.btn} onClick={openLogoutModal}>
        
      </div>
    </div>
  );
});
