import { AuthClient } from "./axiosInstance";

export default class QuestionService {
  static async fetchChatLog(chatroom_id) {
    return AuthClient().get(`/chats/${chatroom_id}`);
  }

  static async fetchChatList() {
    return AuthClient().get(`/chats/chatroom`);
  }

  static async enterChatRoom(question_id, chatroom_id) {
    return AuthClient().post(`/chats/chatroom`, { question_id, chatroom_id });
  }
}