import React, { useState, useMemo, useContext } from "react";

const AuthContext = React.createContext();
const AuthAction = React.createContext();

export function AuthProvider({ children }) {
  const [authState, setAuthState] = useState({
    isLogin: Boolean(
      window.localStorage.getItem(import.meta.env.VITE_TOKEN_KEY_NAME)
    ),
    username: window.localStorage.getItem(import.meta.env.VITE_NICKNAME),
  });

  const authActions = useMemo(
    () => ({
      login({ token, username }) {
        setAuthState({
          isLogin: true,
          username: username,
        });
        window.localStorage.setItem(import.meta.env.VITE_TOKEN_KEY_NAME, token);
        window.localStorage.setItem(import.meta.env.VITE_NICKNAME, username);
      },
      logout() {
        setAuthState({
          isLogin: false,
          username: null,
        });
        window.localStorage.removeItem(import.meta.env.VITE_TOKEN_KEY_NAME);
        window.localStorage.removeItem(import.meta.env.VITE_NICKNAME);
      },
      setUsername(newUsername) {
        setAuthState({
          ...authState,
          username: newUsername,
        });
        window.localStorage.setItem(import.meta.env.VITE_NICKNAME, newUsername);
      },
    }),
    []
  );

  return (
    <AuthAction.Provider value={authActions}>
      <AuthContext.Provider value={authState}>{children}</AuthContext.Provider>
    </AuthAction.Provider>
  );
}

export function useAuth() {
  const authState = useContext(AuthContext);
  const authActions = useContext(AuthAction);

  if (authState === undefined) {
    throw new Error("Auth state is undefined");
  }

  if (authActions === undefined) {
    throw new Error("authActions is undefined");
  }

  return [authState, authActions];
}
