export function elapsedText(date) {
  date = formattedCreatedDate(date);
  const seconds = 1;
  const minute = seconds * 60;
  const hour = minute * 60;
  const day = hour * 24;

  const today = new Date();
  const elapsedTime = Math.trunc((today.getTime() - date.getTime()) / 1000);

  let elapsedText = "";
  if (elapsedTime < seconds) {
    elapsedText = " ";
  } else if (elapsedTime < minute) {
    elapsedText = elapsedTime + " ";
  } else if (elapsedTime < hour) {
    elapsedText = Math.trunc(elapsedTime / minute) + " ";
  } else if (elapsedTime < day) {
    elapsedText = Math.trunc(elapsedTime / hour) + " ";
  } else if (elapsedTime < day * 15) {
    elapsedText = Math.trunc(elapsedTime / day) + " ";
  } else {
    elapsedText = date.toISOString().split("T")[0];
  }

  return elapsedText;
}

function formattedCreatedDate(created_date) {
  let [date, paddingTime] = created_date.split("T");
  let time = paddingTime.split(".")[0];
  date = date.replace(/-/g, "/");

  return new Date(`${date} ${time}`);
}