import React from "react";
import css from "../Login/Login.module.css";
import { useNavigate } from "react-router-dom";
import Input from "../../components/Input/Input";
import Button from "../../components/Button/Button";
import Warning from "../../components/Warning/Warning";
import Toast from "../../components/Toast/Toast";
import Logo from "../../components/Logo/Logo";

export default function SignUpView({
  input,
  onInput,
  onSubmit,
  toastState,
  isLoading,
  sendEmailCode,
  sendDuplicateNameCheck,
  duplicateBtnDisable,
  emailCodeDisable,
  filteredUserName,
  isInvalidEmail,
  isInValidPassword,
  isInValidConfirmPassword,
}) {
  return (
    <main>
      <section className={css.loginPage}>
        <Logo />
        <form className={css.inputs} onSubmit={onSubmit}>
          <div className={css.authRequired}>
            <Input
              type="text"
              name="username"
              value={filteredUserName}
              onChange={onInput}
              placeholder=""
              required={true}
            />
            <Button
              type="button"
              role="emailAuth"
              onClick={sendDuplicateNameCheck}
              disabled={filteredUserName.length < 1 || duplicateBtnDisable}
            >
              
            </Button>
          </div>
          <div className={css.authRequired}>
            <Input
              role="email"
              type="email"
              name="email"
              value={input.email}
              onChange={onInput}
              placeholder=""
              disabled={input.username < 1}
              required={true}
            />
            <Button
              type="button"
              role="emailAuth"
              onClick={sendEmailCode}
              disabled={isInvalidEmail | emailCodeDisable}
            >
               
            </Button>
          </div>
          <Input
            type="verification_code"
            name="verification_code"
            value={input.verification_code}
            onChange={onInput}
            placeholder=" "
            required={true}
          />
          <div>
            <Input
              type="password"
              name="password"
              value={input.password}
              onChange={onInput}
              placeholder=""
              required={true}
            />
            {input.password.length > 0 && isInValidPassword && (
              <Warning>//   8~15</Warning>
            )}
          </div>
          <div>
            <Input
              type="password"
              name="password2"
              value={input.password2}
              onChange={onInput}
              placeholder=" "
              required={true}
            />
            {input.password2.length > 0 && isInValidConfirmPassword && (
              <Warning>  .</Warning>
            )}
          </div>
          <MemoizedButtons isLoading={isLoading} />
        </form>
      </section>
      <div className={css.cheese}>
        <p> Cheese Co.</p>
      </div>
      {/* hack */}
      {toastState.show && <Toast desc={toastState.desc} />}
    </main>
  );
}

const MemoizedButtons = React.memo(({ isLoading }) => {
  const navigate = useNavigate();

  return (
    <div className={css.btns}>
      <Button
        type="button"
        role="loginBack"
        onClick={() => {
          navigate(-1);
        }}
      >
        
      </Button>
      <Button
        type="submit"
        role="signup"
        className={css.loginBtn}
        disabled={isLoading}
      >
        {isLoading ? <div className={css.spinner} aria-hidden /> : ""}
      </Button>
    </div>
  );
});
