import QuestionCard from "./Card/QuestionCard";
import css from "./QuestionList.module.css";

export default function QuestionList({
  questions,
  questionType,
  isCurrentUser,
}) {
  return (
    <section>
      <ul className={css.cardList}>
        {questions.length === 0 ? (
          <div></div>
        ) : (
          questions.map((test) => (
            <QuestionCard
              key={test.pk}
              question={test}
              questionType={questionType}
              isCurrentUser={isCurrentUser}
            />
          ))
        )}
      </ul>
    </section>
  );
}
