import UserService from "../api/UserService";
import isLogin from "../utils/isLogin";

export default async function followAction({ params }) {
  if (!isLogin()) {
    if (window.confirm("   .  ?")) {
      window.location.href = "/";
    } else {
      return;
    }
  }
  try {
    const res = await UserService.followUser(params.username);
    return res.status;
  } catch (err) {
    if (err === 401) {
      if (window.confirm(" .  ?")) {
        window.location.href = "/";
      } else {
        throw new Error(401);
      }
    }
  }
}
