import QuestionService from "../api/QuestionService";
import handleUnAuthorized from "../utils/handleUnAuthorized";

export default async function useAnswer(dispatch, question_id, content) {
  if (content.length === 0) {
    return { isSuccess: false, desc: " ." };
  }

  if (content.length > 100) {
    return { isSuccess: false, desc: " 100 ." };
  }

  try {
    const { data } = await QuestionService.mutateAnswer(question_id, content);
    dispatch({ type: "POST_ANSWER", payload: data });
    return { isSuccess: true, desc: " ." };
  } catch (err) {
    console.error(err);
    if (err.response.status === 401) {
      handleUnAuthorized(err);
    }
  }
}
